import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { StoresService } from './stores.service';

import { FormBuilder } from '@angular/forms';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-stores',
  templateUrl: './stores.component.html',
  styleUrls: ['./stores.component.scss'],
})
export class StoresComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private storesService: StoresService,
    private modalService: BsModalService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private exportAsService: ExportAsService,
    private fb: FormBuilder
  ) {
    this.setNow();
  }

  ngOnInit(): void {
    this.spinner.show();

    this.storesService.getAll();

    this.storesService.stores.subscribe(async (data) => {
      console.log(data);

      if (data) {
        this.items = data;

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter((item) =>
        item.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  discountForm = this.fb.group({
    discountType: [''],
    discountValue: [0],
    discountPercentage: [0],
    validTill: [''],
  });

  minDate = new Date();
  datePickerValue: Date = new Date();
  time: string;
  forPeriod = false;
  setNow() {
    let now = new Date();
    let hours = ('0' + now.getHours()).slice(-2);
    let minutes = ('0' + now.getMinutes()).slice(-2);
    let str = hours + ':' + minutes;
    this.time = str;
  }
  changePeriod(event) {
    if (!event.target.checked) {
      this.discountForm.get('validTill').patchValue(undefined);
    }
  }
  addDiscount() {
    this.spinner.show();

    this.modalRef.hide();

    if (this.forPeriod) {
      var validTill = new Date(
        this.datePickerValue.getFullYear(),
        this.datePickerValue.getMonth(),
        this.datePickerValue.getDate(),
        parseInt(this.time.split(':')[0]),
        parseInt(this.time.split(':')[1]),
        0
      );

      this.discountForm.get('validTill').patchValue(validTill);
    }

    this.storesService
      .addDiscount(this.discountForm.value, this.itemId)
      .subscribe(
        (res) => {
          console.log(res);
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.discountForm.reset();
          this.storesService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  deleteDiscount() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.deleteDiscount(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  lockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.lockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  unlockElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.unlockOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  CollectElement() {
    this.spinner.show();
    this.modalRef.hide();
    this.storesService.collect(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
    this.spinner.hide();
  }

  activateElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.activateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deactivateElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.storesService.deactivateOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.storesService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService.save(this.exportAsConfig, 'Stores').subscribe(() => {
      // save started
    });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
